/* HEADER */
var header = document.querySelector('#header');
var wrapper = document.querySelector('.wrapper');
window.addEventListener('scroll', stickHeader);

function setupHeader(withScroll = false) {
    var headerHeight = parseInt(header.getBoundingClientRect().height);
    var height = withScroll ? headerHeight + 100 + 'px' : headerHeight + 'px';
    wrapper.style.paddingTop = height;
}
setupHeader();

function stickHeader() {
    if (document.documentElement.scrollTop > 99) {
        header.classList.add('fixed');
        setupHeader(true);
    } else {
        header.classList.remove('fixed');
        setupHeader();
    }
}